import { TypeOrmModuleOptions } from '@nestjs/typeorm';
import * as dotenv from 'dotenv';
import { SnakeNamingStrategy } from '../utils/snake-naming.strategy';

// import { IAwsConfig } from '../../interfaces/IAwsConfig';
// import { UserSubscriber } from '../entity-subscribers/user-subscriber';

export class ConfigService {
  constructor() {
    const nodeEnv = this.nodeEnv;
    dotenv.config({
      path: `.${nodeEnv}.env`,
    });

    // Replace \\n with \n to support multiline strings in AWS
    for (const envName of Object.keys(process.env)) {
      process.env[envName] = process.env[envName].replace(/\\n/g, '\n');
    }
  }

  public get(key: string): string {
    return process.env[key];
  }

  public getNumber(key: string): number {
    return Number(this.get(key));
  }

  get nodeEnv(): string {
    return this.get('NODE_ENV') || 'development';
  }

  get typeOrmConfig(): TypeOrmModuleOptions {
    // let entities = [__dirname + '/../../modules/**/*.entity{.ts,.js}'];
    // let migrations = [__dirname + '/../../migrations/*{.ts,.js}'];

    let entities = [__dirname + '/../../modules/**/*.entity{.ts,.js}',__dirname +  '../../libs/api-interfaces/**/*.entity{.ts,.js}'];
    let migrations = [__dirname + '/../../migrations/*{.ts,.js}'];


    if ((<any>module).hot) {
      const entityContext = (<any>require).context('./../../modules', true, /\.entity\.ts$/);
      entities = entityContext.keys().map(id => {
        const entityModule = entityContext(id);
        const [entity] = Object.values(entityModule);
        return entity;
      });
      const migrationContext = (<any>require).context('./../../migrations', false, /\.ts$/);
      migrations = migrationContext.keys().map(id => {
        const migrationModule = migrationContext(id);
        const [migration] = Object.values(migrationModule);
        return migration;
      });
    }

    return {
      entities,
      // migrations,
      keepConnectionAlive: true,
      type: 'mysql',
      port: 3306,
      host: this.get('DB_METO_HOST'),
      username: this.get('DB_METO_USERNAME'),
      password: this.get('DB_METO_PASSWORD'),
      database: this.get('DB_METO_DATABASE'),
      // username: process.env.DB_METO_USERNAME,
      // password: process.env.DB_METO_PASSWORD,
      // database: process.env.DB_METO_DATABASE,
      // subscribers: [UserSubscriber],
      migrationsRun: true,
      logging: this.nodeEnv === 'development',
      namingStrategy: new SnakeNamingStrategy(),
      synchronize: false,
      autoLoadEntities: true,
    };
  }

  // get awsS3Config(): IAwsConfig {
  //   return {
  //     accessKeyId: this.get('AWS_S3_ACCESS_KEY_ID'),
  //     secretAccessKey: this.get('AWS_S3_SECRET_ACCESS_KEY'),
  //     bucketName: this.get('S3_BUCKET_NAME'),
  //   };
  // }
}
